// ุฅุถุงูุฉ ุฎุฏูุฉ ุฌุฏูุฏุฉ
export async function POST(request: NextRequest) {
  try {
    await dbConnect();
    const serviceData = await request.json();
    
    // ุงูุชุญูู ูู ูุฌูุฏ ูุนุฑู ุงูุฎุฏูุฉ
    const existingService = await (ServiceModel as Model<any>).findOne({ id: serviceData.id });
    if (existingService) {
      return new NextResponse(JSON.stringify({
        error: 'ูุนุฑู ุงูุฎุฏูุฉ ูุณุชุฎุฏู ุจุงููุนูุ ุงูุฑุฌุงุก ุงุณุชุฎุฏุงู ูุนุฑู ุขุฎุฑ'
      }), {
        status: 400,
        headers: {
          'Content-Type': 'application/json; charset=utf-8'
        }
      });
    }
    
    // ุฅูุดุงุก ุฎุฏูุฉ ุฌุฏูุฏุฉ
    const newService = await (ServiceModel as Model<any>).create(serviceData);
    
    return new NextResponse(JSON.stringify({ 
      id: newService.id,
      success: true 
    }), {
      headers: {
        'Content-Type': 'application/json; charset=utf-8'
      }
    });
  } catch (error) {
    console.error('ุฎุทุฃ ูู ุฅุถุงูุฉ ุฎุฏูุฉ ุฌุฏูุฏุฉ:', error);
    return new NextResponse(JSON.stringify(
      { error: 'ุญุฏุซ ุฎุทุฃ ุฃุซูุงุก ุฅุถุงูุฉ ุงูุฎุฏูุฉ ุงูุฌุฏูุฏุฉ' }
    ), {
      status: 500,
      headers: {
        'Content-Type': 'application/json; charset=utf-8'
      }
    });
  }
}

// ุชุญุฏูุซ ุฎุฏูุฉ ููุฌูุฏุฉ
export async function PUT(request: NextRequest) {
  try {
    await dbConnect();
    const serviceData = await request.json();
    const { id, ...updateData } = serviceData;
    
    await (ServiceModel as Model<any>).findOneAndUpdate(
      { id: id }, 
      {
        ...updateData,
        updatedAt: new Date()
      }
    );
    
    return NextResponse.json({ success: true });
  } catch (error) {
    console.error('ุฎุทุฃ ูู ุชุญุฏูุซ ุงูุฎุฏูุฉ:', error);
    return new NextResponse(JSON.stringify(
      { error: 'ุญุฏุซ ุฎุทุฃ ุฃุซูุงุก ุชุญุฏูุซ ุงูุฎุฏูุฉ' }
    ), {
      status: 500,
      headers: {
        'Content-Type': 'application/json; charset=utf-8'
      }
    });
  }
}

// ุญุฐู ุฎุฏูุฉ
export async function DELETE(request: NextRequest) {
  try {
    await dbConnect();
    const url = new URL(request.url);
    const id = url.searchParams.get('id');
    
    if (!id) {
      return new NextResponse(JSON.stringify(
        { error: 'ูุนุฑู ุงูุฎุฏูุฉ ูุทููุจ' }
      ), {
        status: 400,
        headers: {
          'Content-Type': 'application/json; charset=utf-8'
        }
      });
    }
    
    await (ServiceModel as Model<any>).findOneAndDelete({ id: id });
    return NextResponse.json({ success: true });
  } catch (error) {
    console.error('ุฎุทุฃ ูู ุญุฐู ุงูุฎุฏูุฉ:', error);
    return new NextResponse(JSON.stringify(
      { error: 'ุญุฏุซ ุฎุทุฃ ุฃุซูุงุก ุญุฐู ุงูุฎุฏูุฉ' }
    ), {
      status: 500,
      headers: {
        'Content-Type': 'application/json; charset=utf-8'
      }
    });
  }
}
